
.home-header {
  position: fixed;
  top: 0;
  width: 100%;
  overflow: hidden;
  z-index: 99;
  border-bottom: 0.01389rem solid #c6c6c6;
}
.home-header .header-search {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    padding: 0.18056rem 0;
    line-height: 0.83333rem;
    text-align: center;
    vertical-align: top;
    background: #d43c33;
}
.cancel {
  position: absolute;
  right: -14%;
  width: 14%;
  color: #fff;
  text-decoration: underline;
  cursor: pointer;
  transition: all .5s;
}
.header-music,
.header-voice {
  position: absolute;
  top: 0;
  display: block;
  line-height: 1.19444rem;
  height: 100%;
  width: 15%;
  cursor: pointer;
}
.header-music .icon,
  .header-voice .icon {
    font-size: 0.625rem;
    color: #fff;
}
.header-music:active .icon,
  .header-voice:active .icon {
    color: #222327;
}
.header-voice {
  left: 0;
}
.header-music {
  right: 0;
  opacity: 1;
  transition: all .3s;
}
.search-group {
  position: relative;
  margin-left: 15%;
  width: 70%;
  transition: all .5s;
}
.search-group input {
    vertical-align: top;
    height: 0.83333rem;
    width: 100%;
    font-size: 0.36111rem;
    border-radius: 10px;
    border: none;
    outline: none;
}
.search-group label {
    position: absolute;
    width: 100%;
    height: 100%;
    color: #ccc;
    font-size: 0.36111rem;
    transition: all .3s;
}
.search-group label .icon {
      padding: 0 0.23611rem;
      font-size: 0.36111rem;
}
.home-header.is-search .header-music {
  opacity: 0;
}
.home-header.is-search .header-voice {
  display: none;
}
.home-header.is-search .search-group {
  margin-left: 2%;
  width: 85%;
}
.home-header.is-search .search-group input {
    padding-left: 0.80556rem;
}
.home-header.is-search .search-group label {
    text-align: left;
}
.home-header.is-search .cancel {
  right: 0;
}
.header-nav {
  position: relative;
  height: 1.11111rem;
  line-height: 1.11111rem;
  background: #f7f8f8;
  box-shadow: 0 0px 30px rgba(0, 0, 0, 0.3);
}
.header-nav .nav-menu {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    font-size: 0.38889rem;
}
.header-nav li {
    width: 25%;
    text-align: center;
}
.header-nav li a {
      display: block;
      width: 100%;
      height: 100%;
      color: #333;
}
.header-nav li span {
      display: inline-block;
      padding: 0 0.15278rem;
}
.header-nav .active a {
    color: #d43c33;
}
.header-nav .nav-slider {
    position: absolute;
    bottom: 0;
    height: 0.05556rem;
    background: #d43c33;
    left: 34px;
    width: 141px;
    transition: all .3s;
}



