
.music-content {
  padding: 1.22222rem 0 1.34722rem;
  font-size: 0.45833rem;
}
.menu-list {
  background: #fbfcfd;
  text-align: left;
}
.menu-list li {
    position: relative;
    line-height: 1.375rem;
    border-bottom: 0.01389rem solid #dfdedf;
}
.menu-list li:last-child {
    border: none;
}
.menu-list a {
    color: #333;
    display: block;
    padding: 0 0.27778rem 0 1.52778rem;
}
.menu-list .icon {
    position: absolute;
    left: 0;
    height: 100%;
    font-size: 0.58333rem;
    padding: 0 0.38889rem 0 0.38889rem;
    color: #d43c33;
    background: #fbfcfd;
}
.menu-list .num {
    float: right;
    font-size: 0.30556rem;
}
.music-sheet .title {
  height: 0.83333rem;
  padding: 0 0.27778rem;
  line-height: 0.83333rem;
  background: #eeeff0;
  color: #666;
  font-size: 0.30556rem;
}
.music-sheet .sheet-list {
  padding: 0.30556rem 0.27778rem;
  background: #fbfcfd;
}
.music-sheet.is-show .sheet-list {
  display: block;
}
.sheet-list {
  display: none;
}
.sheet-list li {
    box-sizing: content-box;
    padding: 0.13889rem 0;
    height: 1.52778rem;
}
.sheet-list .item-img {
    float: left;
    width: 1.38889rem;
    height: 1.38889rem;
    overflow: hidden;
}
.sheet-list .item-img img {
      width: 100%;
}
.sheet-list .item-info {
    box-sizing: content-box;
    height: 1.38889rem;
    margin-left: 1.66667rem;
    padding-bottom: 0.20833rem;
    border-bottom: 0.01389rem solid #dfdedf;
}
.sheet-list .item-title {
    font-size: 0.44444rem;
    line-height: 0.72222rem;
    color: #333;
}
.sheet-list .item-text {
    line-height: 0.48611rem;
    font-size: 0.31944rem;
    color: #888;
}



