
.swiper-container {
  width: 100%;
  height: 100%;
}
.swiper-container img {
    width: 100%;
    height: 100%;
}
.swiper-container .swiper-pagination-bullet {
    margin: 0 0.05556rem;
    width: 0.16667rem;
    height: 0.16667rem;
    background: #fff;
    opacity: .5;
}
.swiper-container .swiper-pagination-bullet-active {
    background: #d43c33;
}



