
@charset "UTF-8";
* {
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
html {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
button,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
  line-height: 1.426;
}
article h1,
article h2,
article h3,
article h4,
article h5,
article h6 {
  margin: 10px 0 15px 0;
}
article h1,
section h1 {
  font-size: 2em;
}
a,
a:hover,
a:active,
a:focus,
input,
input:hover,
input:active,
input:focus,
select,
select:hover,
select:active,
select:focus,
textarea,
textarea:hover,
textarea:active,
textarea:focus {
  outline: none;
}
button,
button:active,
button:focus,
button:hover {
  outline: none;
}
ul,
ol {
  list-style: none;
}
@font-face {
  font-family: 'iconfont';
  /* project id 104803 */
  src: url("//at.alicdn.com/t/font_xh651p968kbg9zfr.eot");
  src: url("//at.alicdn.com/t/font_xh651p968kbg9zfr.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_xh651p968kbg9zfr.woff") format("woff"), url("//at.alicdn.com/t/font_xh651p968kbg9zfr.ttf") format("truetype"), url("//at.alicdn.com/t/font_xh651p968kbg9zfr.svg#iconfont") format("svg");
}
.icon {
  font-family: 'iconfont';
  font-style: normal;
  display: inline-block;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
