html,
body {
  width: 100%;
  height: 100%;
}

/* clearfix [清除浮动]*/
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;
  /*IE/7/6*/
}
.fl {
  float: left;
}
.fl {
  float: right;
}
.tl {
  text-align: left;
}
.tr {
  text-align: right;
}
.text-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.ov-h {
  overflow: hidden;
}
.music-app {
  position: relative;
  min-height: 100%;
  min-width: 100%;
  background: #fbfcfd;
  font-family: 'Microsoft YaHei';
  color: #333;
  font-weight: 500;
  -webkit-font-smoothing: antialiased;
}
.app-router {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  z-index: 999;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .3s;
}



