
.app-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1.34722rem;
  border-top: 0.01389rem solid #231e22;
  overflow: hidden;
}
.footer-bg {
  margin: -50px;
  height: 200%;
  width: 200%;
  background: #202228;
}
.footer-nav {
  position: absolute;
  left: 0;
  top: 0;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  text-align: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.footer-nav .item {
    display: block;
    padding: 0.06944rem 0 0.06944rem;
    width: 25%;
    height: 100%;
    color: #fff;
}
.footer-nav .item .icon {
      font-size: 0.66667rem;
      color: #a7a7a7;
}
.footer-nav .item .title {
      color: #7c7a7b;
}
.footer-nav .item.router-link-active .icon {
    color: #fff;
}
.footer-nav .item.router-link-active .title {
    color: #fff;
}



