
.my-music-header {
  position: fixed;
  top: 0;
  width: 100%;
  line-height: 0.83333rem;
  text-align: center;
  font-size: 0.45833rem;
  color: #fff;
  z-index: 99;
}
.my-music-header .header-group {
    position: relative;
    padding: 0.18056rem 0;
    background: #d43c33;
    z-index: 2;
}
.header-music,
.header-more {
  position: absolute;
  top: 0;
  display: block;
  line-height: 1.19444rem;
  height: 100%;
  width: 15%;
  cursor: pointer;
}
.header-music .icon,
  .header-more .icon {
    font-size: 0.625rem;
    color: #fff;
}
.header-music:active .icon,
  .header-more:active .icon {
    color: #222327;
}
.header-more {
  left: 0;
  text-decoration: underline;
}
.header-music {
  right: 0;
  opacity: 1;
  transition: all .3s;
}
.more-menu {
  position: absolute;
  top: -3.05556rem;
  left: 0;
  width: 100%;
  background: #f7f8f8;
  text-align: left;
  transition: all .3s;
  box-shadow: 0 0px 30px rgba(0, 0, 0, 0.3);
  z-index: 1;
}
.more-menu li {
    position: relative;
    line-height: 1.375rem;
    border-bottom: 0.01389rem solid #dfdedf;
}
.more-menu li:last-child {
    border: none;
}
.more-menu a {
    color: #333;
    display: block;
    padding-left: 1.52778rem;
}
.more-menu .icon {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    font-size: 0.58333rem;
    padding: 0 0.38889rem 0 0.38889rem;
    background: #f8f7f7;
}
.more-menu ~ .header-mask {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  transition: all .5s;
}
.more-menu.is-show {
  display: block;
  top: 1.19444rem;
}
.more-menu.is-show ~ .header-mask {
  display: block;
}



