
.music-control {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.music-control .controller-bg {
    position: absolute;
    width: 150%;
    height: 150%;
    background-image: url("../../assets/img/main.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-color: rgba(0, 0, 0, 0.35);
    background-blend-mode: darken;
    filter: blur(80px);
}
.control-header {
  position: fixed;
  top: 0;
  width: 100%;
  text-align: center;
  font-size: 0.45833rem;
  color: #fff;
  border-bottom: 0.01389rem solid #727173;
  z-index: 99;
}
.control-header .header-group {
    position: relative;
    padding: 0.18056rem 0;
    z-index: 2;
}
.control-header .header-group .icon {
      font-weight: 100;
      vertical-align: -10px;
}
.control-header .header-group .icon-back {
      vertical-align: -15px;
}
.header-back,
.header-share {
  position: absolute;
  top: 0;
  display: block;
  line-height: 1.19444rem;
  height: 100%;
  width: 15%;
  cursor: pointer;
}
.header-back .icon,
  .header-share .icon {
    font-size: 0.625rem;
    color: #fff;
}
.header-back:active .icon,
  .header-share:active .icon {
    color: #222327;
}
.header-back {
  left: 0;
}
.header-share {
  right: 0;
}
.song-info .song-name {
  font-size: 0.41667rem;
}
.song-info .author-name {
  font-size: 0.27778rem;
}



