
.home-content {
  padding: 2.30556rem 0 1.34722rem;
}
.home-content .slider {
    height: 3.83333rem;
    background: #fff;
}
.home-content .Recommend {
    display: -ms-flexbox;
    display: flex;
    text-align: center;
    height: 2.875rem;
    border-bottom: 0.01389rem solid #e1e2e3;
}
.Recommend {
  padding: 0.23611rem 0 0.23611rem;
}
.Recommend .item {
    width: 33.333%;
    padding-top: 0.18056rem;
}
.Recommend .title {
    padding: 0.18056rem 0;
    font-size: 0.31944rem;
}
.img {
  display: inline-block;
  vertical-align: top;
  width: 1.52778rem;
  height: 1.52778rem;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 100%;
}
.img-FM {
  background-image: url("../../assets/img/music_03.jpg");
}
.img-Recommend {
  background-image: url("../../assets/img/music_05.jpg");
}
.img-Hot-top {
  background-image: url("../../assets/img/music_07.jpg");
}
.img-calendar {
  background-image: url("../../assets/img/music_13.jpg");
}
.img-album {
  background-image: url("../../assets/img/music_list_03.jpg");
}
.img-cd {
  background-image: url("../../assets/img/music_list_16.jpg");
}
.img-radio {
  background-image: url("../../assets/img/music_031.jpg");
}
.list-content {
  padding: 0.55556rem 0.30556rem 0;
}
.item-box .title {
  line-height: 0.44444rem;
  font-size: 0.44444rem;
}
.item-box .title .img {
    margin-right: 0.18056rem;
    height: 0.41667rem;
    width: 0.44444rem;
}
.item-box .title .more {
    float: right;
    font-size: 0.31944rem;
    line-height: 0.44444rem;
    color: #666;
    text-decoration: underline;
}
.item-box .list-warp {
  display: -ms-flexbox;
  display: flex;
  padding: 0.27778rem 0;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.item-box .list-warp a {
    color: #333;
}
.song-list li {
  margin-bottom: 0.48611rem;
  width: 2.98611rem;
  height: 4.22222rem;
  overflow: hidden;
}
.song-list .icon-triangle-right {
  position: absolute;
  right: 0.11111rem;
  margin: 0.06944rem 0;
  width: 0.69444rem;
  height: 0.69444rem;
  line-height: 0.625rem;
  font-size: 0.27778rem;
  text-align: center;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.5);
  border: 0.05556rem solid #fff;
}
.song-list .icon-triangle-right:before {
    margin-left: 12px;
}
.song-list .label {
  position: absolute;
  right: 0;
  top: 0;
  display: block;
  width: 60%;
  height: 0.5rem;
  padding-right: 0.08333rem;
  line-height: 0.5rem;
  font-size: 0.27778rem;
  text-align: right;
  color: #fff;
  background: linear-gradient(-180deg, rgba(0, 0, 0, 0.4), transparent);
  background: -webkit-linear-gradient(-180deg, rgba(0, 0, 0, 0.4), transparent);
  text-shadow: 0 0.04167rem 0.02778rem #1b1715;
}
.song-list .label .icon {
    font-size: 0.33333rem;
    vertical-align: -4px;
}
.song-list .label-play {
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  height: 0.83333rem;
  line-height: 0.83333rem;
  padding: 0 0.94444rem 0 0.11111rem;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  font-size: 0.31944rem;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.5), transparent);
}
.song-list .item-img {
  position: relative;
  height: 0;
  width: 100%;
  margin-bottom: 0.15278rem;
  padding-bottom: 100%;
  background: #eee;
}
.song-list .item-img img {
    width: 100%;
}
.song-list .item-title {
  font-size: 0.34722rem;
  line-height: 0.47222rem;
  max-height: 0.94444rem;
}
.song-list .item-title p {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.song-list .item-title p:nth-child(2) {
    color: #a7a7a7;
}
.sole-push .item-img {
  position: relative;
  height: 2.69444rem;
  overflow: hidden;
}
.sole-push .item-img img {
    width: 100%;
}
.sole-push .label {
  position: absolute;
  right: 0;
  top: 0;
  display: block;
  width: 60%;
  height: 0.5rem;
  padding-right: 0.08333rem;
  line-height: 0.5rem;
  font-size: 0.27778rem;
  text-align: right;
  color: #fff;
  background: linear-gradient(-180deg, rgba(0, 0, 0, 0.4), transparent);
  background: -webkit-linear-gradient(-180deg, rgba(0, 0, 0, 0.4), transparent);
  text-shadow: 0 0.04167rem 0.02778rem #1b1715;
}
.sole-push .label .icon {
    font-size: 0.33333rem;
    vertical-align: -4px;
}
.sole-push li {
  width: 4.58333rem;
}
.sole-push li:nth-child(3) {
  width: 100%;
  margin-bottom: 0.48611rem;
  padding: 0.26389rem 0 0;
  border-top: 0.02778rem solid #e1e2e3;
}
.sole-push li:nth-child(3) .item-img {
    height: 3.63889rem;
}
.sole-push li:nth-child(3) .item-title {
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    overflow: hidden;
    line-height: 1.3em;
}
.sole-push .item-title {
  padding: 0.19444rem 0;
  font-size: 0.34722rem;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden;
  line-height: 1.3em;
}
.sole-push.mv li {
  margin-bottom: 0.48611rem;
}
.sole-push.mv li:nth-child(3) {
  width: 4.58333rem;
  margin-bottom: 0.48611rem;
  padding: 0;
  border-top: none;
}
.sole-push.mv li:nth-child(3) .item-img {
    height: 2.69444rem;
}
.sole-push.mv li:nth-child(3) .item-title {
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    overflow: hidden;
    line-height: 1.3em;
}
.sole-push.mv .label .icon {
  padding-right: 0.11111rem;
  vertical-align: -1px;
}
.app-cont-footer {
  padding: 0.41667rem 0 1.11111rem;
  text-align: center;
  font-size: 0.31944rem;
  border-top: 0.01389rem solid #e1e2e3;
}
.app-cont-footer p {
    line-height: 0.55556rem;
    margin-bottom: 0.31944rem;
    color: #999;
}
.app-cont-footer .btn {
    display: inline-block;
    vertical-align: top;
    height: 0.77778rem;
    line-height: 0.77778rem;
    padding: 0 0.48611rem;
    background: #fff;
    color: #d43c33;
    font-size: 0.31944rem;
    border: 0.02778rem solid #d43c33;
    border-radius: 0.06944rem;
    cursor: pointer;
}



